import React from "react"
import { Link } from 'react-router-dom';
import LandscapeIcon from '@material-ui/icons/Landscape';
import PaletteIcon from '@material-ui/icons/Palette';
import TextFieldsIcon from '@material-ui/icons/TextFields';
import WbSunnyIcon from '@material-ui/icons/WbSunny';
import PlayCircleFilledIcon from '@material-ui/icons/PlayCircleFilled';
import Camera from '@material-ui/icons/Camera';
import PublicIcon from '@material-ui/icons/Public';
import SettingsIcon from '@material-ui/icons/Settings';
import AudiotrackIcon from '@material-ui/icons/Audiotrack';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';

import SvgIcon from '@material-ui/core/SvgIcon';
import { ReactComponent as CameraIcon } from 'assets/icons/camera.svg';

export default class Uploading extends React.Component {
    render() {
        return (
            <div className='help-page'>
                <Breadcrumbs aria-label="breadcrumb">
                    <Link to="/help">
                        Help Center
                    </Link>
                    <Link to="/help/editing">
                        Editing
                    </Link>
                </Breadcrumbs>
                <h1>Ezyvue Studio</h1>
                <p>
                    In Ezyvue Studio you can configure various aspects of the scene including the model, materials, environment, lighting, shadows etc. 
                    <br/>To open the editor select a model on the Project Manager page and click the edit button.
                </p>
                <h2>The Editor Layout</h2>
                <img style={{ minWidth: '280px', width: '600px' }} alt='editor' src={require('assets/images/help/editor.jpg')}/>
                <h3>The 3d view</h3>
                <p>
                    The 3d view shows a view of the model from the camera.<br />
                </p>
                <h3>Camera Control</h3>
                <p>See the viewer options <Link to='./viewer'>here.</Link></p>
                <h3>The Sidebar</h3>
                <p>
                    The sidebar interface groups editor features into panels which can be selected using the tab header as shown below.<br />
                </p>
                <p>
                    Click on the links below to view documentation for each tab:
                </p>
                <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                    <div className='help-page-links-row'>
                        <span>
                            <LandscapeIcon size='small' />
                        </span>
                        <Link to='/help/sceneOptions'>Environment settings</Link>
                    </div>
                    <div className='help-page-links-row'>
                        <span>
                            <WbSunnyIcon size='small' />
                        </span>
                        <Link to='/help/lightingOptions'>Lighting settings</Link>
                    </div>
                    <div className='help-page-links-row'>
                        <span>
                            <PublicIcon size='small' />
                        </span>
                        <Link to='/help/editMaterials'>Editing materials</Link>
                    </div>
                    <div className='help-page-links-row'>
                        <span>
                            <PlayCircleFilledIcon size='small' />
                        </span>
                        <Link to='/help/animations'>Playing animations</Link>
                    </div>
                    <div className='help-page-links-row'>
                        <span>
                            <TextFieldsIcon size='small' />
                        </span>
                        <Link to='/help/annotations'>Adding 3d text annotations, labels and images</Link>
                    </div>
                    <div className='help-page-links-row'>
                        <span>
                            <Camera size='small' />
                        </span>
                        <Link to='/help/effects'>Post processing effects</Link>
                    </div>
                    <div className='help-page-links-row'>
                        <span>
                            <PaletteIcon size='small' />
                        </span>
                        <Link to='/help/ProductConfigurator'>Product configurator</Link>
                    </div>
                    <div className='help-page-links-row'>
                        <span>
                            <AudiotrackIcon size='small' />
                        </span>
                        <Link to='/help/audio'>Adding an audio track</Link>
                    </div>
                    <div className='help-page-links-row'>
                        <span>
                            <SvgIcon>
                                <CameraIcon />
                            </SvgIcon>
                        </span>
                        <Link to='/help/cameraOptions'>Camera settings</Link>
                    </div>
                    <div className='help-page-links-row'>
                        <span>
                            <SvgIcon>
                                <SettingsIcon />
                            </SvgIcon>
                        </span>
                        <Link to='/help/options'>General Settings</Link>
                    </div>
                </div>
                <br/>
            </div>
        );
    }
}
